html {
  font-family: 'rockingham', 'Helvetica Neue', Helvetica, Arial, sans-serif; }

*, *:before, *:after {
  box-sizing: border-box; }

html {
  font-size: 112.5%;
  line-height: 1.33333em;
  background: #fff;
  color: #000;
  text-shadow: 0 1px 0 #fff; }

html, body {
  height: 100%; }

a:link, a:visited {
  color: #c00;
  text-decoration: none; }
a:hover, a:focus, a:active {
  color: #ad0000;
  text-decoration: underline; }

.section {
  border-bottom: 2px dashed #c00;
  padding: 4rem 0;
  transition: all 300ms; }
  @media (min-width: 45em) {
    .section {
      padding: 18vh 0; } }

.section-inner {
  margin: 0 auto;
  max-width: 45rem;
  padding: 1.33333rem; }

h1 {
  font-size: 9vw;
  line-height: 12vw;
  color: #c00; }
  @media (min-width: 39.58333em) {
    h1 {
      font-size: 3.16667rem;
      line-height: 3.33333rem; } }

@media (min-width: 45em) {
  [data-location='home'] .page {
    background: url('../images/cover.jpg') no-repeat scroll top right;
    background-size: auto 100%;
    padding: 30vh 0; } }
[data-location='home'] .page em {
  color: #666666;
  font-variant: small-caps; }
  [data-location='home'] .page em:before {
    content: ' | '; }

.release {
  background: linear-gradient(to top, rgba(255, 255, 255, 0.9) 8rem, rgba(255, 255, 255, 0) 18.66667rem) no-repeat scroll, url('../images/tg-jen.jpg') no-repeat scroll;
  background-clip: padding-box;
  background-size: cover;
  padding: 6.66667rem 0 1.33333rem;
  position: relative; }
  @media (min-width: 45em) {
    .release {
      padding: 50vh 0 1.33333rem; } }
  .release .p-name {
    font-size: 5vw;
    line-height: 6.66667vw;
    margin-bottom: 0.66667rem; }
    @media (min-width: 53.75em) {
      .release .p-name {
        font-size: 2.38889rem;
        line-height: 2.66667rem; } }
  .release .sub {
    font-variant: small-caps; }
  .release a:link, .release a:visited {
    font-variant: normal;
    font-weight: bold; }

.call-to-action {
  font-size: 5vw;
  line-height: 6.66667vw;
  background: #fff;
  border: 2px solid #c00;
  font-weight: bold;
  left: 50%;
  padding: 1.33333rem;
  position: absolute;
  top: 0;
  transform: translate3d(-50%, -50%, 0);
  white-space: nowrap; }
  @media (min-width: 40em) {
    .call-to-action {
      font-size: 1.77778rem;
      line-height: 2rem; } }

.p-summary {
  font-variant: small-caps; }

.presenters {
  font-variant: small-caps; }
  .presenters a:link, .presenters a:visited {
    font-size: 9vw;
    line-height: 12vw; }
    @media (min-width: 39.58333em) {
      .presenters a:link, .presenters a:visited {
        font-size: 3.16667rem;
        line-height: 3.33333rem; } }

.event-details {
  list-style: disc;
  margin: 0.66667rem 0; }

.press-kit:link, .press-kit:visited {
  background: #fff;
  border: 2px solid #c00;
  color: #000;
  display: inline-block;
  left: 50%;
  padding: 1.33333rem;
  position: absolute;
  top: 100%;
  transform: translate3d(-50%, -50%, 0);
  transition: all 300ms; }
.press-kit:hover, .press-kit:focus, .press-kit:active {
  text-decoration: none;
  transform: translate3d(-50%, -50%, 0) scale(1.25); }
.press-kit b {
  color: #c00; }

.intro {
  overflow: hidden; }
  .intro b {
    font-weight: bold; }

.pullquote {
  font-size: 4vw;
  line-height: 5.33333vw;
  background: #fff;
  border: 1px solid #666666;
  box-shadow: 1px 1px 6px #666666;
  height: 0;
  margin-bottom: 1.33333rem;
  padding-bottom: 60%;
  position: relative; }
  @media (min-width: 37.5em) {
    .pullquote {
      font-size: 1.33333rem;
      line-height: 2rem; } }
  .pullquote:before, .pullquote:after {
    content: ' ';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #fff no-repeat scroll;
    background-size: 100% auto;
    border: 1px solid #666666;
    box-shadow: 1px 1px 6px #666666;
    transition: all 300ms ease-in-out;
    z-index: -1; }
  .pullquote:before {
    background-image: url('../images/herm.jpg');
    transform: rotate(-1.5deg); }
  .pullquote:after {
    background-image: url('../images/boxer.jpg');
    transform: rotate(1.5deg); }
  .pullquote:hover:before {
    transform: rotate(3deg) translate3d(20%, -20%, 0); }
  .pullquote:hover:after {
    transform: rotate(-3deg) translate3d(-10%, -30%, 0); }
  .pullquote p {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    align-items: center;
    display: flex;
    justify-content: center;
    padding: 0 15%; }

.description {
  color: #666666;
  margin: 0 2.66667rem; }

.blurb + .blurb {
  margin-top: 2.66667rem; }

.blurb-body {
  font-size: 5vw;
  line-height: 6.66667vw;
  font-style: italic;
  margin-bottom: 0.33333rem; }
  @media (min-width: 40em) {
    .blurb-body {
      font-size: 1.77778rem;
      line-height: 2rem; } }
  .blurb-body .open, .blurb-body .close {
    color: #c00;
    font-size: 2.38889rem;
    font-weight: bold;
    line-height: .25; }
  .blurb-body .open {
    display: inline-block;
    margin-left: -1.33333rem;
    width: 1.33333rem; }

.blurb-cite {
  color: #666666; }

[role='contentinfo'] {
  font-size: 0.77778rem;
  line-height: 1.33333rem;
  color: #666666;
  margin: 0 auto;
  max-width: 45rem;
  padding: 2.66667rem 1.33333rem;
  text-transform: uppercase; }
  [role='contentinfo'] strong, [role='contentinfo'] b, [role='contentinfo'] .p-name {
    font-weight: bold; }

.publisher a:link, .publisher a:visited {
  font-weight: bold; }

@keyframes blur {
  0%, 60%, 75%, 80%, 100% {
    filter: blur(0); }

  70% {
    filter: blur(1.5px); }

  95% {
    filter: blur(3px); } }

@keyframes shake {
  0%, 5%, 42%, 48%, 100% {
    transform: translate3d(0, 0, 0); }

  10% {
    transform: translate3d(-1px, -3px, 0); }

  20% {
    transform: translate3d(-5px, 0, 0); }

  30% {
    transform: translate3d(0, 3px, 0); }

  40% {
    transform: translate3d(1px, -1px, 0); }

  50% {
    transform: translate3d(-1px, 3px, 0); }

  60% {
    transform: translate3d(-5px, 1px, 0); }

  70% {
    transform: translate3d(3px, 1px, 0); }

  80% {
    transform: translate3d(-1px, -1px, 0); }

  90% {
    transform: translate3d(3px, 3px, 0); }

  100% {
    transform: translate3d(1px, -3px, 0); } }

.art {
